<template>
    <div>
        <h1>할 일 리스트</h1>
        <form  @submit.prevent="addTodo()">
            <input type="text" placeholder="해야할 일들을 적어봅시다" v-model="newTodo"/><button type="submit">추가</button>
        </form>
        <ul>
            <TodoItem v-for="(todo, index) in todos" :key=index :todo="todo" @toggle="toggleTodo" />
        </ul>
        <button @click="removeChecked" class="delete">완료한 것만 삭제하기</button>
    </div>
</template>

<script>
    import TodoItem from './TodoItem.vue';

    export default{
        components:{
            TodoItem
        },
        data(){
            return{
                newTodo:'',
                todos:[]
            }
        },
        created(){
            const storedTodos=localStorage.getItem('todos');
            if(storedTodos){
                this.todos=JSON.parse(storedTodos);
            }
        },
        methods:{
            savedTodos(){
            localStorage.setItem('todos',JSON.stringify(this.todos));
            },
            addTodo(){
            if(this.newTodo.trim()){
                this.todos.push({
                    text:this.newTodo.trim(),
                    checked: false
                });
                this.newTodo='';
                this.savedTodos();
            }
            },
            toggleTodo(todo){
                todo.checked = !todo.checked;
                this.savedTodos();
            },
            removeChecked(){
                this.todos = this.todos.filter(todo => !todo.checked);
                this.savedTodos();
            }
        }
    }
</script>

<style scoped>
    h1{
        font-size: 24px; padding-top: 20px;
        margin-bottom: 20px;
    }
    form{
        width: 100%; display: flex;
        justify-content:center; align-items:center;
    }
    form>input{
        border: 0px none; width:75%;
        padding:10px 10px 10px 15px; line-height:18px;
        font-size: 18px; border-radius:10px 0px 0px 10px;
    }
    form>button{
        width: 41px; height: 41px;
        background-color:#666;
        color: #fff; border-radius:0px 10px 10px 0px;
    }
    ul{
        width: 90%; list-style: none;
        margin:20px auto 0px;
    }
    button.delete{
        background-color: #f40; color: #fff; font-weight: bold;
        font-size: 20px; padding:10px 20px; border-radius: 20px;
    }
</style>