<template>
    <li :class="{checked:todo.checked}">
        <p>{{ todo.text }}</p>
        <label v-if="todo.checked===true">
            <input type="checkbox" @change="toggleCheck" :checked="todo.checked"><font-awesome-icon :icon="['fas', 'check']"/>
        </label>
        <label v-else>
            <input type="checkbox" @change="toggleCheck" :checked="todo.checked"><span>■</span>
        </label>
    </li>
</template>

<script>

export default{
    props:{
        todo:{
            type:Object, // type:String에서 변경 -- text:string, checked:boolean 상태 필요
            required:true,
        },
    },
    methods:{
        toggleCheck(){
            this.$emit('toggle', this.todo);
            //this.todo.checked;
        },
    }
}
</script>

<style scoped>
    li{
        width:100%; min-height:40px;
        line-height: 40px; text-align: center; font-size:20px; font-weight: bold;
        display: flex; justify-content:space-between; align-items:center; flex-wrap:nowrap;
        background-color: #fff; border-radius: 10px; margin-bottom: 10px; overflow: hidden;
        box-sizing: border-box; border: 1px solid #333;
    }
    li.checked{
        background-color: #fc0;
    }
    li>p{
        text-decoration: none;  padding-left: 10px;
    }
    li.checked>p{
        text-decoration-thickness:2px !important; text-decoration:line-through; color:#888;
    }
    li>label>input{
        display: none;
    }
    li>label{
        display: block; width: 40px; height: 40px;
        background-color: #ccc; font-size: 16px;
        position: relative; color: #fff;
    }
    li>label>span{
        display: inline-block; font-size:16px;
    }
    li.checked>label{
        background-color:#09f;
    }
</style>